}

.todo-item input[type="checkbox"] + label {
    display: inline-block;
    position: relative;
    cursor: pointer;
}

.todo-item input[type="checkbox"] + label:before {
    content: ' ';
    display: inline-block;
    width: 1rem;
    height: 1rem;
    line-height: 1rem;
    margin: -2px 8px 0 0;
    text-align: center;
    vertical-align: middle;
    background: transparent;
    border: 0.15rem solid rgba(51,51,51,0.5);
    border-radius: 2%;
}

.todo-item input[type="checkbox"]:checked + label:before {
    content: '\2714';
    color:gray;
    border: 0.15rem solid rgba(51,51,51,0.2);
}

