html {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 15px;
}

body {
  margin: 0px;
  background-color: #FBFBFD;

  color: #333333;

  text-align: center;
}

ul {
  padding: 0px;
}

h1 {
  font-size: 3rem;
}

.main {
  margin-top: 10%;
  margin-bottom: 15%;    
}

.dark {
  background-color: #121212;
  color: #ffffff;
}

.template-wrapper {
  position: relative;
  z-index: 1;
  width: 600px;
  margin: auto;
  margin-top: 3%;

  text-align: center;

  border-radius: 10%;

  padding: 1%;
}

.template-wrapper-dark {
  position: relative;
  z-index: 1;
  width: 600px;
  margin: auto;
  margin-top: 3%;

  text-align: center;

  background-color: #1D1D1D;
  border-radius: 10%;

  padding: 1%;
}

.component {
  margin-top: 12.5%;
  margin-bottom: 20%;    
}

/*Responsive*/
@media screen and (min-width: 768px) and (max-width: 1023px) {
  h1 {
      font-size: 2.8rem;
  }

  .component {
    margin-top: 15%;
    margin-bottom: 20%;    
  }

  .template-wrapper {
    width: 80%;
  }

  .template-wrapper-dark {
    width: 80%;
  }
}
@media screen and (max-width: 767px) {
  html {
      font-size: 14px;
  }

  h1 {
      font-size: 2.2rem;
  }

  .component {
    margin-top: 20%;
    margin-bottom: 25%;    
  }

  .template-wrapper {
    width: 90%;
  }

  .template-wrapper-dark {
    width: 90%;
  }
}
